@import "./functions";

@mixin resetLink {
  text-decoration: none;
  color: inherit; }

@mixin resetButton {
  cursor: pointer;
  padding: 0;
  border: 0;
  background: none; }

@mixin resetList {
  list-style: none;
  margin: {
    top: 0;
    bottom: 0; }
  padding-left: 0; }

@mixin rwdImage {
  width: 100%;
  height: auto; }

@mixin clearfix {
  &::after {
    content: '';
    display: block;
    clear: both; } }

@mixin pxRem($prop, $px) {
  #{$prop}: $px;
  #{$prop}: px2rem($px); }
