@import "../../styles/mixins";

.headerbar {
  &__homelink {
    margin: {
      top: 0;
      bottom: 0; }
    &--link {
      display: block;
      @include pxRem(padding, 10px); }
    &--logo {
      display: inherit;
      @include pxRem(width, 54px); }

    @media (min-width: 1024px) {
      margin-left: -10px;
      &--logo {
        @include pxRem(width, 82px); } } } }
