@import "@styles/config";

.headlineSection {
  &__title {
    margin-top: 0;
    @include pxRem(font-size, 20px);
    line-height: 1;

    color: $colorLinkActive; }
  &__description {
    @include pxRem(font-size, 13.5px);
    line-height: 1.54;

    color: $colorDescription; }
  a[href] {
    @include resetLink;
    color: $colorLinkColorful; } }

